* {
  margin: 0;
  padding: 0;
}

body {
  grid-template-columns: repeat(12, 1fr);
  font-family: Poppins;
}

.home, .contact, .five-year {
  background-blend-mode: screen;
  background-color: #889696;
  background-image: url("hero-image-min.f06f61f0.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  grid-template-columns: repeat(12, 1fr);
  display: grid;
}

header {
  height: 100px;
  grid-column: span 12;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
  padding: 10px;
  display: grid;
}

header nav {
  grid-column: 5 / span 7;
  justify-content: space-around;
  padding: 10px;
  font-size: 2em;
  display: flex;
}

nav a {
  color: #333;
  justify-self: end;
  font-weight: 800;
  text-decoration: none;
}

.main {
  gap: 20dppx;
  grid-column: 1 / span 12;
  grid-template-columns: repeat(12, 1fr);
  display: grid;
}

.welcome {
  width: 100%;
  grid-column: 4 / span 9;
  margin-top: 30px;
  font-size: 1.5rem;
  font-weight: 800;
  line-height: 1.5em;
}

.welcome h1 {
  width: 100%;
  margin-top: 50px;
  margin-bottom: .5em;
  font-family: Poppins;
  font-size: 4em;
  font-weight: 800;
}

.welcome p {
  max-width: 860px;
}

.welcome img {
  filter: saturate(50%);
}

nav a:hover {
  color: #fff;
}

.card {
  max-width: 100%;
  height: 260px;
  opacity: .7;
  background-color: #ddd;
  border-radius: 6px;
  flex-direction: column;
  grid-column: span 4;
  justify-content: space-between;
  align-items: center;
  margin-top: 80px;
  margin-left: 40px;
  margin-right: 20px;
  padding: 15px;
  font-size: 1em;
  font-weight: 800;
  line-height: 1.5em;
  display: flex;
  box-shadow: 3px 3px 3px #0009;
}

button {
  width: 200px;
  height: 50px;
  border-radius: 6px;
  font-weight: 800;
  box-shadow: 4px 4px 4px #0009;
}

button:hover {
  filter: brightness(1.8);
  box-shadow: 1px 1px 1px #0000004d;
}

#projects-card button {
  margin-bottom: 5px;
}

.warranty {
  min-width: 100%;
  opacity: .8;
  background-color: #ddd;
  border-radius: 6px;
  grid-column: 2 / span 9;
  grid-template-columns: 1fr;
  justify-content: flex-start;
  margin-top: 50px;
  padding: 30px 60px;
  display: grid;
}

.warranty h1 {
  opacity: 1;
  grid-column: span 1;
  justify-self: center;
  font-size: 2em;
}

.warranty h3 {
  font-style: bold;
  align-self: flex-start;
}

.warranty p {
  margin-top: 10px;
  margin-bottom: 10px;
}

h5 {
  font-style: italic;
}

form {
  height: 600px;
  opacity: .8;
  background-color: #ddd;
  border-radius: 6px;
  grid-column: 2 / span 10;
  grid-template-columns: repeat(12, 1fr);
  margin: 0 auto;
  padding: 25px;
  display: grid;
  position: relative;
  top: 50px;
  bottom: 450px;
}

form input {
  height: 30px;
  background-color: #fff;
  border-bottom: 2px solid #000;
  border-radius: 6px;
  grid-column: 6 / span 6;
  padding: 5px;
}

#message {
  height: 120px;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 6px;
  grid-column: 6 / span 6;
  padding: 5px;
}

#contact-info {
  width: 100%;
  grid-column: 1 / span 4;
  font-weight: 800;
  position: absolute;
  top: 10px;
}

#contact-info p {
  margin-top: 20px;
  line-height: 1em;
}

form button {
  grid-column: 8 / span 2;
}

.history {
  grid-template-columns: 1.2fr 1fr 1fr;
  margin: 0;
  padding: 0;
  font-family: Poppins;
  display: grid;
}

.panel {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.panel.introduction {
  background-color: #323230;
  padding: 30px 60px;
}

.panel.introduction h1 {
  color: #f5f5f5;
  font-size: 4em;
}

.panel.introduction a {
  color: #a8a8a8;
  margin-right: 10px;
  font-size: 1.5em;
}

.panel.introduction a:hover {
  color: #fff;
}

.panel.introduction p {
  color: #a8a8a8;
  max-width: 540px;
  line-height: 2em;
}

.panel.introduction footer {
  color: #a8a8a8;
  align-self: end;
}

.panel.about {
  grid-template-rows: .8fr 4fr;
  overflow: hidden;
}

.panel.about img {
  min-width: 100%;
  grid-row: span 1;
}

.panel.leadership {
  background-color: #eeeee7;
  grid-template-rows: 1fr .8fr .8fr;
  padding: 0;
  overflow: hidden;
}

.panel.leadership img {
  min-width: 100%;
  max-width: 100%;
}

.text {
  max-width: 100%;
  max-height: 100%;
  color: #323230;
  grid-row: span 1;
  padding: 10px;
}

.products-body {
  background-color: #ddd;
  background-image: url("welding-min.b7afafb5.png");
  background-size: cover;
  background-position: fixed;
  background-blend-mode: luminosity;
  background-color: #f5f5f5;
  background-repeat: no-repeat;
  grid-template-columns: 1fr 1fr;
  display: grid;
}

.product-section {
  height: 100px;
  color: #000;
  opacity: .8;
  background-color: #a8a8a8;
  border-radius: 6px;
  flex-direction: row;
  grid-column: 2 / span 1;
  justify-content: flex-start;
  align-items: center;
  margin-top: 100px;
  padding: 15px;
  font-weight: 800;
  display: flex;
  box-shadow: 3px 3px 3px 3px #0009;
}

.product-section a {
  margin-left: 30px;
  font-size: 2em;
}

.product-section a:hover {
  color: #fff;
}

.product-section img {
  max-height: 100px;
  border-radius: 6px;
}

.products-header {
  grid-column: 1 / span 2;
  grid-template-columns: repeat(12 1fr);
  height: 0;
  display: grid;
  position: absolute;
}

.products-header nav {
  grid-column: span 12;
  justify-self: start;
}

.products-header nav a {
  color: #000;
  margin-right: 30px;
  font-size: 1.5em;
}

.products-header nav a:hover {
  color: #fff;
}

.product-section.one {
  margin-top: 300px;
}

@media screen and (max-width: 1200px) {
  .welcome h1 {
    font-size: 3em;
  }

  .welcome img {
    height: 100px;
    width: 100px;
  }

  header nav {
    grid-column: 2 / span 9;
  }

  .panel.introduction nav {
    font-size: .7em;
  }

  .panel.introduction h1 {
    font-size: 3em;
  }

  .product-section a {
    font-size: 1.5em;
  }
}

@media screen and (max-width: 992px) {
  .welcome h1 {
    font-size: 2.5em;
  }

  .welcome img {
    height: 80px;
    width: 80px;
  }

  .card {
    font-size: .8em;
  }

  header nav {
    grid-column: 1 / span 12;
  }

  .panel.introduction nav {
    height: 0;
    flex-direction: column;
    display: flex;
  }

  .panel.introduction {
    justify-content: space-between;
  }

  form {
    grid-column: span 12;
  }

  form input, #message {
    grid-column: 7 / span 5;
  }

  .product-section {
    grid-column: 1 / span 2;
    margin-top: 50px;
  }

  .product-section.one {
    margin-top: 200px;
  }

  .product-section a {
    font-size: 1.2em;
  }

  .products-body header nav a {
    font-size: 1.1em;
  }
}

@media screen and (max-width: 768px) {
  .welcome h1 {
    font-size: 2em;
  }

  header nav a {
    font-size: .8em;
  }

  .card {
    font-size: .75em;
  }

  .card button {
    max-width: 100%;
  }

  .history {
    grid-template-columns: 1fr 1fr;
    display: grid;
  }

  .panel.introduction, .panel.about {
    grid-column: span 1;
  }

  .panel.leadership {
    grid-column: span 2;
  }

  form {
    padding: 10px;
  }

  .warranty {
    grid-column: 1.5 / span 11;
    padding: 15px;
    font-size: .8em;
  }

  .warranty p {
    max-width: 100%;
    grid-column: span 12;
    justify-self: center;
  }

  .card {
    margin-left: 20px;
    margin-right: 20px;
  }

  li {
    margin-left: 10px;
  }

  .products-body {
    height: 600px;
    background-position: center;
  }

  .products-body header nav a {
    font-size: 1em;
  }

  .product-section {
    height: 80px;
    grid-column: 1 / span 2;
    margin-top: 20px;
  }
}

@media screen and (max-width: 576px) {
  .panel.introduction, .panel.about {
    grid-column: span 2;
  }

  .panel.introduction nav {
    height: 150px;
  }

  .panel.introduction footer {
    margin-top: 40px;
  }

  .card {
    margin-left: 10px;
    margin-right: 10px;
  }

  .welcome {
    grid-column: 2 / span 12;
  }

  .welcome h1 {
    font-size: 2em;
  }

  .welcome img {
    height: 60px;
    width: 60px;
  }

  .home header nav, .five-year header nav, .contact header nav {
    height: 110px;
    grid-template-columns: 1fr 1fr;
    justify-self: start;
    font-size: 1.5em;
    display: grid;
  }

  .home header nav a, .five-year header nav a, .contact header nav a {
    grid-column: 1 / span 2;
    justify-self: start;
    margin-right: 10px;
  }

  form {
    height: 550px;
    margin-top: 50px;
  }

  form input, #message {
    grid-column: 8 / span 5;
  }

  .product-section {
    height: 80px;
    grid-column: 1 / span 2;
    margin-top: 0;
  }

  .products-body {
    height: 660px;
  }

  .product-section {
    height: 70px;
    margin-top: 0;
  }

  .product-section img {
    height: 80px;
  }
}

@media screen and (max-width: 390px) {
  .card {
    grid-column: span 12;
    font-size: 1em;
  }

  .welcome img {
    height: 40px;
    width: 40px;
  }

  .welcome h1 {
    font-size: 1.5em;
  }

  #contact-info {
    grid-column: 1 / span 12;
    position: relative;
  }

  form input, #message {
    grid-column: 1 / span 12;
    margin-top: 10px;
  }

  form button {
    grid-column: 5 / span 4;
  }

  .products-body {
    height: 1380px;
    background-size: cover;
    grid-auto-rows: 300px;
  }

  .product-section {
    width: 600px;
    height: 100px;
    align-self: start;
    margin-top: 250px;
    font-size: 2em;
  }

  .product-section.one {
    margin-top: 600px;
  }

  .product-section.two {
    margin-top: 450px;
  }

  .product-section.three {
    margin-top: 300px;
  }
}

